<template>
  <div>
    <br>
    <br>
    <el-form ref="registerForm" :model="register" label-width="80px">
      <el-form-item label="申请人">
        <el-input v-model="register.applicant"></el-input>
      </el-form-item>
      <el-form-item label="项目组">
        <el-input v-model="register.orginaztion"></el-input>
      </el-form-item>
      <el-form-item label="Key前缀">
        <el-input v-model="register.keyPrefix"></el-input>
      </el-form-item>
      <el-form-item label="应用名称">
        <el-input v-model="register.appName"></el-input>
      </el-form-item>
      <el-form-item>
          <el-button type="primary" @click="appRegister">申请</el-button>
          <el-button type="primary" @click="cancel">取消</el-button>
        </el-form-item>
    </el-form>


  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
          return {
            register: {
                      applicant: '',
                      orginaztion: '',
                      keyPrefix: '',
                      appName: '',

                    }
          }
        },
        methods:{
          appRegister(){
            const that = this
            axios.post('http://localhost:8088/application/register', {
              mail:this.register.applicant,
              Organization:this.register.orginaztion,
              KeyPrefix:this.register.keyPrefix,
              applicationName:this.register.appName
            }).then(function (response){
              if (response.status == 200) {
                console.log(response.status)
                console.log(response.data)
                that.$router.push({
                  name:'RegisterTips',
                  params:{htmlStr: response.data}

                })
              }


            })
          },
          cancel() {
            console.log("cancel")
             this.$refs['registerForm'].resetFields()
            // this.$refs['registerForm'].resetFields()
          }
        }
  }
</script >


<style>
</style>



