<template>
  <div>
        <el-form :inline="true" style="margin-left: 2%;">
          <el-row>
            <el-form-item label="集群">
              <el-select v-model="selectCluster" placeholder="请选择集群">
                  <el-option
                    v-for="item in clusters"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="Key">
               <el-input  placeholder="请输入key" v-model="keyInput" clearable> </el-input>
            </el-form-item>
            <el-form-item label="value">
               <el-input  placeholder="请输入value" v-model="keyValue" clearable> </el-input>
            </el-form-item>
            <el-form-item>
                <el-button size="small" type="primary" @click="queryKey(keyInput)">查询</el-button>

            </el-form-item>
            <el-form-item>
                <el-button size="small" type="primary" @click="addKey(keyInput,keyValue)">新增</el-button>
            </el-form-item>
          </el-row>
        </el-form>
        <!--列表-->
        <template>
         <el-table :data="keys" style="width: 100%;">
          <el-table-column prop="id" label="key" align="center"/>
          <el-table-column prop="value" label="value" align="center"/>
          <el-table-column prop="ttl" label="ttl(second)" align="center"/>
          <!-- <el-table-column prop="size" label="size(byte)" align="center"/> -->
          <el-table-column label="operate" width="150">
           <template slot-scope="scope"><el-button-group>
            <el-button type="text" size="mini" style="margin-right:20px" @click="editFunc(scope.$index, scope.row)"><i class="el-icon-edit"></i>编辑</el-button>
            <el-button type="text" size="mini" style="color:red" @click="deleteFunc(scope.$index, scope.row)"><i class="el-icon-delete"></i>删除</el-button>
            </el-button-group>
           </template>
          </el-table-column>
         </el-table>
        </template>

        <!--编辑弹出页面-->
        <el-dialog title="key 编辑"
                   :visible.sync="dialogVisible"
                   :before-close="closeDialogFunc">
            <el-form label-width="100px"
                     :model="dataForm"
                     ref="dataForm">
            <el-form-item label="key"
                        prop="id">
            <el-input :disabled="true" v-model="dataForm.id" />
            </el-form-item>
            <el-form-item label="value"
                          prop="value">
              <el-input v-model="dataForm.value" />
            </el-form-item>
            <el-form-item label="ttl(second)"
                          prop="ttl">
              <el-input v-model="dataForm.ttl" />
            </el-form-item>
          </el-form>
          <span slot="footer"
                class="dialog-footer">
            <el-button @click="closeDialogFunc"
                       class="concellbtn">取 消</el-button>
            <el-button @click="saveFunc"
                       class="savebtn">确 定</el-button>
          </span>
        </el-dialog>


  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
          return {
            clusters: this.$store.getters.getClusters,
            selectCluster: '',
            keyInput: '',
            keyValue:'',
            dialogVisible: false,
            keys: [], //list
            dataForm: {//
              'id': '', 'value': '', 'ttl': ''
            }

          }
        },
        methods:{
          queryKey:function(key) {
            axios.get('http://localhost:8088/key/key?key=' + key + '&cluster=' + this.selectCluster)
            .then(resp => {//数据直接赋值
                console.log("resp===" + resp)
                 if (resp.data.code == -2) {
                   this.$alert("key no exists.")
                   return;
                 }
                 if (resp.data.code == -1) {
                    this.$alert(data.msg);
                    return;
                 }
                this.keys = resp.data.data

              })
          },
          addKey:function(key,value) {
            axios.post('http://localhost:8088/key/key', {
              id:key,
              value:value,
              clusterName:this.selectCluster
            }).then(function (response){
              console.log(response)
            }).catch(function(error){
              console.log(error)
            }).finally(() => {
                this.dialogVisible = false
                this.queryKey(key,this.selectCluster)
              })

          },
          deleteFunc: function(index, row) {
            this.$confirm('确认删除该记录吗?', '提示', {
              type: 'warning'
            }).then(() => {

              console.log(row.id)
              axios.delete('http://localhost:8088/key/key',
              {
                data:{
                  id:row.id,
                  clusterName:this.selectCluster
                }
              })
              .finally(() => {
                 this.keys = []
              })
            })
          },
          editFunc: function(index, row) {//新建&编辑
            if (row == undefined) {
              let tmpDataForm = this.dataForm //赋值变量，修改tmpDataForm即可
              Object.keys(tmpDataForm).forEach(function(key){
                tmpDataForm[key] = ''
              })
              this.dialogVisible = true
            } else {

              console.log(this.keys[index])
              this.dataForm = this.keys[index]
              this.dialogVisible = true
          }},
          closeDialogFunc: function () { // 重置校验，并隐藏窗口
            this.$refs['dataForm'].resetFields()
            this.dialogVisible = false
          },
          saveFunc: function () {
            this.$refs['dataForm'].validate((valid) => {
              if (!valid) { return false }
              let tmpDataForm = this.dataForm // 赋值变量
              axios.post('http://localhost:8088/key/key', {
                ttl:tmpDataForm['ttl'],
                id:tmpDataForm['id'],
                value:tmpDataForm['value'],
                clusterName:this.selectCluster
              }).then(function (response){
                console.log(response)
              }).catch(function(error){
                console.log(error)
              }).finally(() => {
                  this.dialogVisible = false
                  this.queryKey(tmpDataForm['id'],this.selectCluster)
                })
            })
          },
          mounted () {
            $(window).newresize(this.autoResize, 200)
            // 参数说明(resize方法，resize一次的时间)
          },
          destroyed () {
            $(window).unbind('resize')
            // 离开组件或者不需要执行resize时间时销毁事件
          }
        }
  }
</script >


