import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

let store = new Vuex.Store({
  state:{
    cart: JSON.parse(localStorage.getItem('cart')) || [],
    clusters:[{
              value: 'codis-demo',
              label: 'codis-demo'
            }, {
              value: 'codis',
              label: 'codis'
            }, {
              value: 'crm',
              label: 'crm'
            }, {
              value: 'kooup',
              label: 'kooup'
            }, {
              value: 'eb',
              label: 'eb'
            },
            {
              value: 'live',
              label: 'live'
            }]
  },
  mutations:{
    addCart(state,item){
      let good = state.cart.find(v => v.text == item.text)
      if(good) {
        good.count += 1
      } else {
        state.cart.push({...item,count:1})
      }
    },
    cartadd(state,index){
      state.cart[index].count += 1
    },
    cartsub(state,index){
      state.cart[index].count -= 1
    }

   },
  getters:{
    cartTotal:state=>{
      let sum = 0
      state.cart.forEach(v=>{
        sum += v.count
      })
      return sum
    },
    getClusters:state => state.clusters
  }
})
store.subscribe((mutations,state)=>{
  localStorage.setItem('cart',JSON.stringify(state.cart))
})
export default store



