<template>
  <div>
        <el-form :inline="true" style="margin-left: 2%;">
          <el-row>
            <el-form-item label="应用名称">
              <el-input  placeholder="请输入应用名称" v-model="appName" clearable> </el-input>
            </el-form-item>
            <el-form-item label="审批状态">
              <el-select v-model="selectAppStatus" placeholder="请选择">
                  <el-option
                    v-for="item in appStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>

            </el-form-item>

            <el-form-item>
              <el-button size="small" type="primary"  @click="queryAppInfo">查询</el-button>
            </el-form-item>

          </el-row>
        </el-form>
        <br><br>

        <el-table :data="apps" style="width: 100%;">
         <el-table-column prop="application_name" label="应用名称" align="center"/>
         <el-table-column prop="secret" label="密钥" align="center"/>
         <el-table-column prop="applicant" label="申请人" align="center"/>
         <el-table-column prop="access_keys" label="访问的key" align="center"/>
          <el-table-column prop="CreateAt" label="创建时间" align="center"/>
         <el-table-column prop="status" label="审批状态(0:未通过;1:通过)" align="center"/>
         <el-table-column label="审批" width="150">
          <template slot-scope="scope"><el-button-group>
           <el-button type="text" size="mini" style="margin-right:20px" @click="approval(scope.$index, scope.row)"><i class="el-icon-edit"></i>审批</el-button>
           </el-button-group>
          </template>
          </el-table-column>
        </el-table>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    data() {
          return {
            appName:'',
            apps:[], //list
            selectAppStatus:'',
            appStatus:[{
                      value: '1',
                      label: '通过'
                    }, {
                      value: '0',
                      label: '未通过'
                    }]

          }
        },
        methods:{
          queryAppInfo(){
            console.log(this.appName)
            //如果是管理员，并且没有输入　appName 查询所有
            //如果是普通员共，并且没有输入appName 查询归属的所有
            if (typeof(this.appName)=="undefined" || this.appName=='' || this.appName==null){

              //获取登陆用户权限，后期需要修改？？
              axios.get('http://localhost:8088/user/app?flag=all')
              .then(resp => {//数据直接赋值
                  console.log("resp===" + resp)
                  this.apps = []
                  this.apps = resp.data.data

                })

            } else {
              axios.get('http://localhost:8088/user/app?flag=' + this.appName)
              .then(resp => {//数据直接赋值
                  console.log("resp===" + resp)
                  this.apps = []
                  this.apps = resp.data.data

                })
            }
          },
          approval: function(index, row) {

            console.log(this.batchs[index].ID)
            // 调用审批接口，处理返回状态
            // axios.get('http://localhost:8088/key/detail?batchId=' + this.apps[index].ID )
            //   .then(resp => {//数据直接赋值
            //       console.log("resp===" + resp)

            //       this.batchDetails = resp.data.data

            //   })

          }

        }
  }
</script>

<style>
</style>



