<template>
  <div class="login" clearfix>
      <div class="login-wrap">
       <el-row type="flex" justify="center">
          <el-form ref="loginForm" :model="user"  status-icon label-width="80px">


            <h4>登录</h4>
            <hr>
            <el-form-item prop="username" label="用户名">
              <el-input v-model="user.username" placeholder="请输入用户名" prefix-icon></el-input>
            </el-form-item>
            <el-form-item id="password" prop="password" label="密码" >
              <el-input v-model="user.password" show-password placeholder="请输入密码"></el-input>
            </el-form-item>
            <el-col :span="12">
              <el-form-item >
                <el-button  type="text"  @click="userLogin()">登录</el-button>

              </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item>
                    <el-button type="text" @click="userRegister()">注册</el-button>
                </el-form-item>
              </el-col>
          </el-form>

       </el-row>
      </div>
    </div>

</template>

<script>
  import axios from "axios"
  export default {
    data() {
        return {
          user: {
            username: "",
            password: ""
          }
        };
      },
    created() {},
    methods: {
        userLogin() {
          if (!this.user.username) {
            this.$message.error("请输入用户名！");
            return;
          } else if (!this.user.password) {
            this.$message.error("请输入密码！");
            return;
          } else {
            axios
              .post("/users/login", {
                mail: this.user.username,
                password: this.user.password
              })
              .then(res => {
                // console.log("输出response.data.status", res.data.status);
                if (res.data.code === 0) {
                  this.$router.push({ path: "/home" });
                } else {
                   alert(res.data.msg);

                }
              });
          }
        },
        userRegister() {
          this.$router.push({ path: "/user/register" });

        }
      }
  }
</script>

<style scoped>
  .login {
  /*  width: 100%;
    height: 100%; */
    background: url("../assets/bg.png") no-repeat;
   /* background-size: cover;

    overflow: hidden; */
     background-size: 100% 100%;
     height: 100%;
     position: fixed;
     width: 100%;
  }
  .login-wrap {
    /* background: url("../assets/a.jpg") no-repeat; */
    background-size: cover;
    width: 400px;
    height: 350px;
    margin: 215px auto;
    overflow: hidden;
    padding-top: 10px;
    line-height: 40px;
  }
  #password {
    margin-bottom: 15px;
  }
  h4 {
    color: #0babeab8;
    font-size: 24px;
  }
  hr {
    background-color: #444;
    margin: 20px auto;
  }
  a {
    text-decoration: none;
    color: #aaa;
    font-size: 15px;
  }
  a:hover {
    color: coral;
  }
  .el-button {
    width: 100%;
    margin-left: -50px;
  }
</style>



