<template>

      <div>
        <br>
        <br>
          <el-form ref="userRegisterForm" :model="user"  label-width="80px">
            <h3>注册</h3>

            <el-form-item prop="username" label="用户名">
              <el-input v-model="user.username" placeholder="请输入邮箱前缀"></el-input>
            </el-form-item>
            <el-form-item prop="keyPrefix" label="key前缀">
              <el-input v-model="user.keyPrefix" placeholder="请输入key前缀"></el-input>
            </el-form-item>
            <el-form-item prop="organization" label="项目组">
              <el-input v-model="user.organization"  placeholder="请输入所在项目组"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"  @click="doRegister()">注册</el-button>
            </el-form-item>
          </el-form>

      </div>

</template>

<script>
  import axios from "axios";
  export default {
    name: "login",
    data() {
      return {
        user: {
          username: "",
          keyPrefix: "",
          organization: ""
        },
      };
    },
    created() {
      // console.log($);
      // console.log("1111");
    },
    methods: {
      doRegister() {
        if (!this.user.username) {
          this.$message.error("请输入用户名！");
          return;
        } else if (!this.user.keyPrefix) {
          this.$message.error("请输入key前缀！");
          return;
        } else if (!this.user.organization) {
          this.$message.error("请输入所在的项目组！");
          return;
        }
         else {
            axios
              .post("/users", {
                mail: this.user.username,
                keyPrefix: this.user.keyPrefix,
                project: this.user.organization
              })
              .then(res => {
                // console.log("输出response.data", res.data);
                // console.log("输出response.data.status", res.data.status);

                if (res.data.code === 0) {
                  //判断用户属否登陆，如果登陆了证明是管理员则不跳转？？
                  this.$router.push({ path: "/"});
                } else {
                  alert(res.data.msg);
                }
              });
          }
        },
      }
    }



</script>
<style scoped>

</style>



