<template>
  <div>
        <el-form :inline="true" style="margin-left: 2%;">
          <el-row>
            <el-form-item label="用户名称">
              <el-input  placeholder="请输入用户名称" v-model="userName" clearable> </el-input>
            </el-form-item>
            <el-form-item label="审批状态">
              <el-select v-model="selectuserStatus" placeholder="请选择">
                  <el-option
                    v-for="item in userStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>

            </el-form-item>

            <el-form-item>
              <el-button size="small" type="primary"  @click="queryAppInfo">查询</el-button>
            </el-form-item>

          </el-row>
        </el-form>
        <br><br>

        <el-table :data="users" style="width: 100%;">
         <el-table-column prop="user_name" label="用户名称" align="center"/>
         <el-table-column prop="access_keys" label="Key前缀" align="center"/>
         <el-table-column prop="organization" label="项目组" align="center"/>
         <el-table-column prop="role_type" label="角色(0:普通用户;1:管理员)" align="center"/>
          <el-table-column prop="CreateAt" label="创建时间" align="center"/>
         <el-table-column prop="status" label="审批状态(0:未通过;1:通过)" align="center"/>
         <el-table-column label="审批" width="150">
          <template slot-scope="scope"><el-button-group>
           <el-button type="text" size="mini" style="margin-right:20px" @click="approval(scope.$index, scope.row)"><i class="el-icon-edit"></i>审批</el-button>
           </el-button-group>
          </template>
          </el-table-column>
        </el-table>
  </div>
</template>

