  import axios from 'axios'

  export default {
    data() {
          return {
            userName:'',
            users:[], //list
            selectuserStatus:'',
            userStatus:[{
                      value: '1',
                      label: '通过'
                    }, {
                      value: '0',
                      label: '未通过'
                    }]

          }
        },
        methods:{
          queryAppInfo(){
            console.log(this.userName)
              axios.get('/users?name=' + this.userName + '& status=' + this.userStatus)
              .then(resp => {//数据直接赋值
                  console.log("resp===" + resp)
                  this.apps = []
                  this.apps = resp.data.data

                })

          },
          approval: function(index, row) {

            console.log(this.batchs[index].ID)
            // 调用审批接口，处理返回状态
            // axios.get('http://localhost:8088/key/detail?batchId=' + this.apps[index].ID )
            //   .then(resp => {//数据直接赋值
            //       console.log("resp===" + resp)

            //       this.batchDetails = resp.data.data

            //   })

          }

        },
     }
</script>

