<template>
  <div>
    <h2>购物车</h2>

 <!-- <table border="1">
    <tr>
    <td>商品</td>
    <td>单价</td>
    <td>价格</td>
    <td>总量</td>
    <td>操作</td>
    </tr>
    <tr v-for=" (c,index) in cart">
      <td>{{c.text}}</td>
       <td>{{c.price}}</td>
        <td>{{c.price * c.count}}</td>
         <td>{{c.count}}</td>
         <td>
           <button @click="sub(index)">-</button>
            <button @click="add(index)">+</button>
         </td>
    </tr>
     <p>总价{{total}}元</p>
  </table> -->
  <el-table :data="cart" stripe style="width: 100%"
      :summary-method="getSummaries"
      show-summary>
    <el-table-column prop="text" label="职位" width="180"></el-table-column>
     <el-table-column prop="price" label="价格" width="180"></el-table-column>
     <el-table-column prop="count" label="数量" width="180"></el-table-column>
     <el-table-column  label="商品总价" width="180">
       <template slot-scope="scope">
         {{scope.row.count * scope.row.price}}
       </template>
     </el-table-column>
     <el-table-column
           label="操作"
           width="180">
           <template slot-scope="scope">
             <el-button @click="add(scope.$index)" type="text" size="small">增加</el-button>
             <el-button @click="sub(scope.$index)" type="text" size="small">减少</el-button>
           </template>
         </el-table-column>
  </el-table>
   <!-- <p>总价{{total}}元</p> -->
  </div>
</template>

<script>
  import {mapState} from 'vuex'

   export default {

    computed:{
      ...mapState({
        cart:state=>state.cart
      }),
      total(){
        return this.cart.reduce((sum,v)=>{
          return sum + v.price * v.count
        },0)
      }
    },
    methods:{
      add(i){
        // this.$emit('add',{index:i})
        this.$store.commit('cartadd',i)
      },
      sub(i){
        // this.$emit('sub',{index:i})
        if (this.cart[i].count >= 1)
          this.$store.commit('cartsub',i)
      },
      getSummaries(param) {
              const { columns, data } = param;
              const sums = [];

              columns.forEach((column, index) => {
                console.log(column)
                console.log(index)
                if (index === 0) {
                  sums[index] = '总价';
                  return;
                }
                if (index == 3){
                   sums[index] = this.cart.reduce((sum,v)=>{
                      return sum + v.price * v.count
                      },0);
                   return;
                }

                const values = data.map(item => Number(item[column.property]));
                if (column.id == "table_1_column_4") {
                  sums[index] = values.reduce((prev, curr) => {
                    const value = Number(curr);
                    if (!isNaN(value)) {
                      return prev + curr;
                    } else {
                      return prev;
                    }
                  }, 0);
                  sums[index] += ' 元';
                } else {
                  sums[index] = 'N/A';
                }
              });

              return sums;
            }
    }
   }
</script>

<style>
</style>



