<template>
  <div>
        <el-form :inline="true" style="margin-left: 2%;">
          <el-row>
            <el-form-item label="集群">
              <el-select v-model="selectCluster" placeholder="请选择集群">
                  <el-option
                    v-for="item in clusters"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
              <el-button size="small" type="primary"  @click="uploadFile">批量删除</el-button>
            </el-form-item>
            <el-upload
              ref="uploadKeys"
              class="upload-keys"
              drag
              action="http://localhost:8088/key/upload"
              multiple
              :limit=1
              :auto-upload="false"
              accept=".txt"
              :before-upload="beforeUploadFile"
              :on-change="fileChange"
              :on-exceed="exceedFile"
              :on-success="handleSuccess"
              :on-error="handleError"
               :file-list="fileList">
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
              <div class="el-upload__tip" slot="tip">只能上传txt文件，文件中每行包含一个key,且大小不能超过1M</div>
            </el-upload>

          </el-row>
        </el-form>
        <br><br>

        <!--列表-->
        <!-- <template>
         <el-table :data="batchs" style="width: 100%;">
          <el-table-column prop="ID" label="批次ID" align="center"/>
          <el-table-column prop="user_name" label="操作人" align="center"/>
          <el-table-column prop="file_name" label="文件名" align="center"/>
          <el-table-column prop="status" label="处理状态(1:完成;0:待处理)" align="center"/>
           <el-table-column prop="CreateAt" label="创建时间" align="center"/>
          <el-table-column label="明细" width="150">
           <template slot-scope="scope"><el-button-group>
            <el-button type="text" size="mini" style="margin-right:20px" @click="batchDetail(scope.$index, scope.row)"><i class="el-icon-edit"></i>查看</el-button>
            </el-button-group>
           </template>
          </el-table-column>
         </el-table>
        </template> -->

        <!--编辑弹出页面-->
        <!-- <el-dialog title="批量处理明细"
                   :visible.sync="dialogVisible"
                   :before-close="closeDialogFunc">
          <el-table :data="batchDetails" style="width: 100%;">
           <el-table-column prop="batch_id" label="批次ID" align="center"/>
           <el-table-column prop="key_name" label="Key" align="center"/>
           <el-table-column prop="status" label="处理状态(1:完成;0:待处理)" align="center"/>
            <el-table-column prop="UpdateAt" label="处理时间" align="center"/>
           </el-table-column>
          </el-table>
          <span slot="footer"
                class="dialog-footer">
            <el-button @click="closeDialogFunc"
                       class="concellbtn">关闭</el-button>
          </span>
        </el-dialog> -->
        <Batch :batchs="batchs"></Batch>
  </div>
</template>

<script>
  import Batch from './Batch'
  import axios from 'axios'
  export default {
    components:{
      Batch
    },
    data() {
          return {
            clusters: this.$store.getters.getClusters,
            selectCluster: '',
            fileList: [],
            form: {
                    file: ''
                  },
            dialogVisible: false,
            batchs: [], //list
            batchDetails:[],

          }
        },
        methods:{
         // 文件超出个数限制时的钩子
             exceedFile(files, fileList) {
               this.$notify.warning({
                 title: '警告',
                 message: `只能选择 ${this.limitNum} 个文件，当前共选择了 ${files.length + fileList.length} 个`
               });
             },
             // 文件状态改变时的钩子
             fileChange(file, fileList) {
               console.log('change')
               console.log(file)
               this.form.file = file.raw
               console.log(this.form.file)
               console.log(fileList)
             },
             // 上传文件之前的钩子, 参数为上传的文件,若返回 false 或者返回 Promise 且被 reject，则停止上传
             beforeUploadFile(file) {
               console.log('before upload')
               console.log(file)
               let extension = file.name.substring(file.name.lastIndexOf('.')+1)
               let size = file.size / 1024 / 1024
               if(extension !== 'txt') {
                 this.$notify.warning({
                   title: '警告',
                   message: `只能上传（后缀是.txt）的文件`
                 });
               }
               if(size > 1) {
                 this.$notify.warning({
                   title: '警告',
                   message: `文件大小不得超过1M`
                 });
               }
             },
             // 文件上传成功时的钩子
             handleSuccess(res, file, fileList) {
              var str = JSON.stringify(res)
              var jsonObj = JSON.parse(str)
               console.log("respons:----" + jsonObj)
               console.log("response code:"+jsonObj.code)
               console.log("response data:" + jsonObj.data)

               if (jsonObj.code == 0) {
                  this.batchs = jsonObj.data
                  this.$notify.success({
                    title: '成功',
                    message: `批量删除成功,请通过批次记录查询处理情况`
                  });
                 this.$refs.uploadKeys.clearFiles()
               } else {
                 this.$notify.success({
                   title: '失败',
                   message: `批量删除失败`
                 });
               }

             },
             // 文件上传失败时的钩子
             handleError(err, file, fileList) {
               this.$notify.error({
                 title: '错误',
                 message: `批量删除失败`
               });
             },
             uploadFile() {
               this.$refs.uploadKeys.submit()

             },
             batchDelete(){
              console.log("batch delete")
             },
             closeDialogFunc: function () { // 重置校验，并隐藏窗口
               // this.$refs['dataForm'].resetFields()
               this.dialogVisible = false
             },

             //查看明细，显示明细table
             batchDetail: function(index, row) {
               if (row == undefined) {
                 let tmpDataForm = this.dataForm //赋值变量，修改tmpDataForm即可
                 Object.keys(tmpDataForm).forEach(function(key){
                   tmpDataForm[key] = ''
                 })
                 this.dialogVisible = true
               } else {
                 console.log(this.batchs[index].ID)
                 axios.get('http://localhost:8088/key/detail?batchId=' + this.batchs[index].ID )
                 .then(resp => {//数据直接赋值
                     console.log("resp===" + resp)

                     this.batchDetails = resp.data.data

                   })
                 this.dialogVisible = true
             }},
        }
  }
</script>

<style>
</style>



