<template>
  <div>
        <el-form :inline="true" style="margin-left: 2%;">
          <el-row>
            <el-form-item label="批次ID">
              <el-input  placeholder="请输入批次ID" v-model="batchId" clearable> </el-input>
            </el-form-item>
            <el-form-item>
              <el-button size="small" type="primary"  @click="queryBatchInfo">查询</el-button>
            </el-form-item>

          </el-row>
        </el-form>
        <br><br>

        <Batch :batchs="batchs"></Batch>
  </div>
</template>

<script>
  import Batch from './Batch'
  import axios from 'axios'
  export default {
    components:{
      Batch
    },
    data() {
          return {
            batchId:'',
            batchs: [], //list

          }
        },
        methods:{
          queryBatchInfo(){
            console.log(this.batchId)
            if (typeof(this.batchId)=="undefined" || this.batchId=='' || this.batchId==null){
              console.log("query all")
              axios.get('http://localhost:8088/key/batch')
              .then(resp => {//数据直接赋值
                  console.log("resp===" + resp)
                  this.batchs = resp.data.data

                })

            } else {
              axios.get('http://localhost:8088/key/batch/id?batchID=' + this.batchId)
              .then(resp => {//数据直接赋值
                  console.log("resp===" + resp)
                  this.batchs = resp.data.data

                })
            }
          },


        }
  }
</script>

<style>
</style>



