import Vue from 'vue'
import Router from 'vue-router'
import Home from '../view/Home'
import Cart from '../view/Cart'
import Key from '../view/Key'
import BatchDelete from '../view/BatchDelete'
import BatchManage from '../view/BatchManage'
import AppRegister from '../view/AppRegister'
import AppRegisterTips from '../view/AppRegisterTips'
import AppQuery from '../view/AppQuery'
import Login from '../view/Login'
import UserRegister from '../view/UserRegister'
import UserAdd from '../view/UserAdd'
import UserInfo from '../view/UserInfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children:[
        {
          path: '/key',
          name: 'key',
          component: Key,
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/key/batch/delete',
          name: 'BatchDelete',
          component:BatchDelete,
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/key/batch/manage',
          name: 'BatchManage',
          component:BatchManage,
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/app/register',
          name: 'AppRegister',
          component:AppRegister,
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/app/register/tips',
          name: 'AppRegisterTips',
          component:AppRegisterTips,
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/app',
          name: 'AppQuery',
          component:AppQuery,
          meta: {
            requireAuth: true
          }
        },
        {
          path:'/user/add',
          name: 'UserAdd',
          component:UserAdd
        },
        {
          path:'/user/info',
          name: 'UserInfo',
          component:UserInfo
        }
      ]
    },
    {
      path:'/user/register',
      name: 'UserRegister',
      component:UserRegister
    }
  ]
})



